import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      logined:false
  },
  mutations: {
      LOGIN(state){
        state.logined = true
      },
      LOGOUT(state){
        state.logined = false
      }
  },
  actions: {
  },
  modules: {

  }
})
